<template>
  <div class="trialpreson flex-v page conclusions"
    style="position:relative">
    <div class="headertitle flex">
      <el-breadcrumb separator-class="el-icon-arrow-right"
        class="my-breadcrumb">
        <el-breadcrumb-item :to="{name:'TotalCheck'}">总检台</el-breadcrumb-item>
        <el-breadcrumb-item>检查结论</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="flex rightbtn">
        <div class="marginright50"
          @click="golookbhyy('驳回原因')">
          <span><img src="@/assets/img/bohuiyyIcon.png" /></span>
          查看驳回原因
        </div>
        <div class="marginright50"
          @click="golookbhyy('复查内容')">
          <span><img src="@/assets/img/fcxqIcon.png" /></span>
          查看复查详情
        </div>
        <div @click="reviewrReport">
          <span style="margin-right:5px">
            <img src="@/assets/img/ylbgIcon.png" />
          </span>
          预览报告
        </div>
      </div>
    </div>
    <div class="tabledata">
      <div class="rightconcent flex-v">
        <div class="toppresonxinxi"
          style="border:none">
          <div class="presonIcon flex-v-center">
            <img v-if="ponserData.addOn1"
              :src="ponserData.addOn1"
              alt=''>
            <template v-else>
              <img v-if="ponserData.sexType==2"
                src="@/assets/img/girlbackImg.png" />
              <img v-else
                src="@/assets/img/boxbackImg.png" />
            </template>
          </div>
          <div class="presoncontent flex-item">
            <div class="cell-item-list">
              <div class="cell-item">
                <label class="lable">姓名 : </label>
                <template class="template">{{ponserData.userName}}</template>
              </div>
              <div class="cell-item">
                <label class="lable">性别 : </label>
                {{
                  ponserData.sexType == 1 ? '男' :
                  ponserData.sexType == 2 ? '女' : '不详'
                }}
              </div>
              <div class="cell-item">
                <label class="lable">年龄 : </label>
                <template class="template">{{ponserData.age}}</template>
              </div>
              <div class="cell-item">
                <label class="lable">居民类型 : </label>
                <template class="template">
                  {{ponserData.personType | getBoxType}}
                </template>
              </div>
              <div class="cell-item">
                <label class="lable"
                  style="min-width:62px">既往史 : </label>
                <template class="template">
                  {{historyDisease | emptyData}}
                </template>
              </div>
              <div class="cell-item">
                <label class="lable">总审医生 : </label>
                <template class="template ">
                  {{ponserData.twoAuthUserName}}
                </template>
              </div>
              <div class="cell-item">
                <label class="lable">总审日期 : </label>
                <template class="template ">
                  {{ponserData.updateDate | formatDate}}
                </template>
              </div>
            </div>
          </div>
        </div>
        <div style="overflow: auto;flex:1">
          <div class="jccentent">
            <div class="centent-title">
              <i class="icon iconfont bj-jcjl-icon"></i>检查结论
            </div>
            <div class="jccentent-list">
              <div class="jccentent-item "
                v-for="item in projectList"
                :key="item.sysGuid">
                <div class="flex">
                  <div>{{item.pname}}：</div>
                  <div class="flex-item">
                    <pre style='white-space:pre-wrap'>{{concatData(item.smallProject, 'checkConclusion')}}</pre>
                  </div>
                </div>
                <div class="flex">
                  <div>建议：</div>
                  <div class="flex-item">
                    <pre style='white-space:pre-wrap'>{{concatData(item.smallProject, 'checkProposal')}}</pre>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="jccentent"
            style="padding-top:10px">
            <div class="centent-title">
              <i class="icon iconfont bj-wxts-icon"
                style="font-size: 20px;"></i>
              温馨提示
            </div>
            <div class="jccentent-list">
              <div class="jccentent-item flex"
                v-for="item in projectList"
                :key="item.sysGuid">
                <div>{{item.pname}}：</div>
                <div class="flex-item">
                  <pre style='white-space:pre-wrap'>{{concatData(item.smallProject, 'checkWaring')}}</pre>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <el-dialog :visible.sync='showPreview'
      class="flex"
      title="体检报告"
      width="1120px"
      center>
      <preview-report :examUserId='sysGuid'
        ref="report" />
      <div slot="footer">
        <el-button class='btn-hollowing'
          @click="showPreview=false">取消</el-button>
        <el-button class='btn-info'
          @click="$refs.report.printPage()">打印</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import PreviewReport from './previewReport'
import { calcType } from '@/lib/utils'
export default {
  name: 'conclusions',
  components: { PreviewReport },
  data() {
    return {
      centerDialogVisible: false,
      tctitle: '',
      ponserData: '', //人员信息
      sysGuid: '',
      projectList: [],
      showPreview: false
    }
  },

  created() {
    this.sysGuid = this.$route.query.sysGuid
    this.getData()
  },

  methods: {
    concatData(list, key) {
      return list.map(item => item[key]).join(' ')
    },
    golookbhyy(title) {
      let { backReason, recheckContent } = this.ponserData
      let msg = title === '驳回原因' ? backReason : recheckContent
      if (!msg) return this.$message({ message: `没有${title}`, type: 'warning' })
      this.$alert(msg, title, {
        showClose: false,
        customClass: 'msg-box danger'
      })
    },
    yesevent() {
      this.centerDialogVisible = false
    },
    getData() {
      let url = '/sys/project/getShowExam'
      let params = { sysGuid: this.sysGuid }
      this.$post(url, params)
        .then(({ data }) => {
          let { user, bigProject, smallProject } = data
          this.ponserData = user
          this.projectList = bigProject.map(item => {
            let t = {
              sysGuid: item.sysGuid,
              pname: item.pname
            }
            t.smallProject = smallProject.filter(items => items.parentId === item.sysGuid)
            return t
          })
        })
        .catch(error => {
          this.$message.error(error)
        })
    },
    reviewrReport() {
      this.showPreview = true
    }
  },

  computed: {
    // 处理既往史
    historyDisease() {
      if (!Object.keys(this.ponserData).length) return ''
      let aTemp = []
      ;[
        'historyIllnessJson',
        'historyOperationJson',
        'historyTransfusionJson',
        'historyTraumaJson'
      ].forEach(item => {
        try {
          let arr = JSON.parse(this.ponserData[item])
          aTemp = aTemp.concat(arr.map(t => t.name))
        } catch (err) {}
      })
      return aTemp.join('/')
    }
  },

  filters: {
    getBoxType(val) {
      let { nameList } = calcType(val)
      return nameList ? nameList.join('/') : '未分配'
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.conclusions {
  .headertitle {
    align-items: center;
    justify-content: space-between;
    .rightbtn {
      display: flex;
      .marginright50 {
        margin-right: 50px;
      }
      div {
        cursor: pointer;
        display: flex;
        align-items: center;
        font-size: 14px;
        color: #666666;
        span {
          width: 25px;
          height: 60px;
        }
      }
    }
  }
  .jccentent {
    padding: 20px 30px;
    .centent-title {
      font-size: 16px;
      color: #666666;
      margin-bottom: 30px;
      .bj-jcjl-icon,
      .bj-wxts-icon {
        margin-right: 12px;
        color: #a6a6a6;
        font-size: 16px;
      }
    }
    .jccentent-list {
      font-size: 14px;
      color: #666666;
      margin-left: 26px;
      border-bottom: dotted 1px #ececee;
      .jccentent-item {
        div {
          margin-bottom: 5px;
        }
      }
    }
    .jccentent-list:last-child {
      border-bottom: none;
    }
  }
  .yesbtn {
    background-color: #00d0a6;
    color: #fff;
    border: 1px solid #00d0a6;
  }
  .yesbtn:hover {
    background-color: #2cdfbb;
    border: 1px solid #2cdfbb;
  }
  .yymain {
    text-align: center;
  }
  .yytitle {
    margin: 20px 0 30px 0;
    color: #fd3905;
    font-size: 22px;
  }
  .yytext {
    color: #666666;
    font-size: 14px;
  }
}
</style>


